import React from 'react';
import { Link } from 'react-router-dom';
import '../style.css';

class Header extends React.Component {
    state = { scroll: "scroll-none" }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
    }
    
    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    handleScroll = () => {
        if(window.scrollY > 30) {
            this.setState({ scroll: "scroll-change" });
        } else {
            this.setState({ scroll: "scroll-none" });
        }
    }
    fixScroll = () => {
        window.scrollTo(0,0);
    }

    render() {
        return (
            <div className={`ui header-component ${this.state.scroll}`}>
                <div className="ui secondary menu">
                    <div className="portfolio-name"><h3><Link target="_self" className="name" to='/'>Cynthia Deng</Link></h3></div>
                    <div className="right menu">
                    <Link target="_self" className="item" to='/'>Home </Link>
                    <Link target="_self" onClick={this.fixScroll} className="item" to='/projects'>Projects</Link>
                    <Link target="_self" className="item" to='/about'>About</Link>
                    </div>
                </div>
            </div>
        );
    }  
}

export default Header;