import React from 'react';

const Contact = () => {
    return (
        <div className="ui container portfolio-home">
            <div className="portfolio-intro ui container stackable grid">
                <div className="sixteen wide column contact-desc">
                    <div className="row contact-intro">
                        <h1>About me</h1>
                        <p>
                        I graduated in UBC with a degree in Cognitive Systems - a multidisciplinary
                        major that incorporates Linguistics, Philosophy, Psychology and Computer Science. I specialized in the Computer Science stream and developed an interest in Web Development and User Research.</p>
                        <p> 
                        Having experience in UX allows me to better empathize with users while developing web applications. Whenever possible, I try to maintain UX principals in all fields of work. I also enjoy learning about new technologies and doing social good. 
                        </p>
                        <p>
                        In my spare time I enjoy volunteering, finding good foodie spots in Vancouver, and (more recently) preparing emotionally for the last season of Game of Thrones.
                        </p>
                    </div>
                    <div className="row contact-intro">
                        <p>
                            <i className="linkedin icon"></i>
                            <a className="contact-linkedin" href="https://www.linkedin.com/in/cynthia-deng/" target="_blank" rel="noopener noreferrer">
                                Connect with me on LinkedIn
                            </a>
                        </p>
                        <p>
                            <i className="github icon"></i>
                            <a className="contact-linkedin" href="https://github.com/Cynthiad215/" target="_blank" rel="noopener noreferrer">
                                GitHub
                            </a>
                        </p>
                        <p><i className="envelope icon"></i>Email: Cynthiad215@gmail.com</p>
                    </div>
                </div>
            </div>
         </div>
    );
};

export default Contact;