import React from 'react';
import "../../style.css";
import donorslogo from '../../images/logo_u2.png';
import fieldresearch from '../../images/ux_field.jpg';
import prototype1 from '../../images/prototype1_main.png';
import prototype2 from '../../images/Prototype2_homepage.png';

const Donors = props => {
    return (
        <div className="project">
            <div className="ui two column grid">
                <div className="eleven wide column">
                    <h1 className="project-title">Donors to Donations</h1>
                    <h3>Evaluating the Effectiveness of Search Strategies on Interfaces</h3>
                </div>
                <img className="right floated column logo-image" alt="donors logo" src={donorslogo}/>
            </div>

            <h4><em>My Role and Contributions</em>:</h4>
            <ul>
                <li>Actively participating in brainstorming and deciding on research design</li>
                <li>Prepared the proposal which was chosen to be completed as project</li>
                <li>Created prototypes using Microsoft Axure</li>
                <li>Actively participating in data analysis of raw data</li>
                <li>Organizing team meetings</li>
            </ul>

            <h4><em>Accomplishments</em>:</h4>
            <ul>
                <li>Winning ‘Most Polished Project’ in a project showcase</li>    
                <li>Interviewing stakeholders from various nonprofit organizations</li>
                <li>Teamwork (with 5 students in a computer-related field)</li>
                <li>Research Design</li>
            </ul>

            <h4><em>Keywords</em>:</h4>
            <p>User Interviews, prototyping with Microsoft Axure, R Studio, Statistical tests, Research Design </p>

            <h4><em>Introduction</em></h4>
            <p>The initial idea for our project originated when discussing ideas that would benefit organizations that could use an online platform to streamline efficiency. The project idea became - creating an online platform for nonprofit organizations to list the donations they needed so that donors could easily see what items would be most useful to donate. The website - named Donors-to-Donations, would aim to streamline donations by facilitating a closer and more fulfilling relationship between donors and nonprofit organizations.</p>

            <h4><em>The Problem</em></h4>
            <p>While conducting market research, we found that most donation websites had little information on what they needed. Although there were organizations that collect monetary donations, many did not list items accepted even though the organization accepted item donations.</p>

            <h4><em>Field Research</em></h4>
            <p>To further understand our users, we conducted interviews with non profit organizations and donors to see what that pain points were in the donation process. One of the major problems was the disconnect between what was being donated and what was needed by the organization. There was a desire for organizations to educate donors on item donations, as many items would need to be manually checked by staff for quality. Donors also wanted to donate to causes they feel would have the most positive impact.</p>
            <p>Based on feedback, we focused on implementing a centralised platform for donors to view a list of items needed by an organization by priority. Monetary goals with details on what the money would be used for which would ideally reduce the skepticism in donating money.</p>
            <img className="project-image" alt="field research" src={fieldresearch} />
            <h4><em>Prototypes</em></h4>
            <p>The goal of our experiment was to learn how best to help users search for charities meet their donation needs. The goal was to learn which approach was easier to understand and most effective in successfully finding what one was looking for. Two med-fi prototypes were created for testing, using Microsoft Axure and covering a wide breadth and depth of functionality for different search strategies which we were testing.</p>

            <p>Prototype 1 aims to integrate the search within one page to handle both donation types. There is a search bar at the top of the page for users to directly search what they’re looking for, with the option to select a category of nonprofits. Users can enter items they are interested in donating and filter search results by items. Upon expanding the donation center ‘cards’, campaigns for monetary donations would be visible.</p>             
           
            <img className="project-image" alt="field research" src={prototype1} />
            <div className="project-wrapper">
                <button className="ui project-button button">
                    <a href="https://yab0q9.axshare.com/homepage.html" target="_blank" rel="noopener noreferrer">Link to Prototype 1</a>
                </button>
            </div>

            <p>Prototype 2 focused on how to display both items and monetary needs for each organization. We decided to use tabs to create separate search pages. Both the “Items” and “Campaign” pages filter similarly, with the ability to filter or search by query.</p>
            <img className="project-image" alt="field research" src={prototype2} /> 
            <div className="project-wrapper">
                <button className="ui project-button button">
                    <a href="https://u83rh4.axshare.com/home.html" target="_blank" rel="noopener noreferrer">Link to Prototype 2</a>
                </button>
            </div>

            <h4><em>User Testing</em></h4>
            <p>The experiment is a 2-way within subject design. To counterbalance between the two prototypes, we constrained randomization to form equal-sized, random groups that will use either the separated or integrated design first. Our independent variables were donation task, and our prototype design. The donation type will be randomly assigned. Dependent variables for our experiment are error count, count of searches made, and satisfaction rating of the user.</p>
            <p>Participants were given tasks with all participants using both prototypes and performed two tasks - one to make an item donation and one to make a monetary donation. Participants were asked to verbalise their thought processes. A short questionnaire followed each tasks to measure satisfaction and opinion of the participant. 
            </p>

            <h4><em>Results</em></h4>
            <p>We ran statistical tests with R Studio on searches made, user error, and prototype satisfaction with R Studio. Our results did not find significance in any of the tests but our users expressed certain aspects of each prototype that they found were well streamlined.</p>

            <p>For example, in Prototype 1, participants generally liked the flexibility that the blended search tools provided. On Prototype 2, participants mentioned the colourful suggestion cards as a main asset to the design. In addition, a successful interface would need to incorporate elements that stood out as being favourable on both prototypes, such as the map, the item tags, and the suggested search terms.</p>

            <p>Both prototypes displayed pros and cons, and future iterations of the design would include a blend of the favoured design aspects and also a balance of complexity and simplicity.</p>

            <p>For our detailed step-by-step findings, visit our <a href="http://blogs.ubc.ca/cs444pentabyte/" target="_blank" rel="noopener noreferrer">blog website</a>!</p>
            
        </div>
    );
}

export default Donors;